import React, { useEffect, useState } from "react";
import "./ArchivedItem__ImgCarousel.css";

export default function ArchivedItem__ImgCarousel({ images }) {
  // Offset
  const [offset, setOffset] = useState(0);

  // Method
  // offset을 조정해서 Carousel Element 왼쪽 이동
  const moveLeft = () => {
    if (offset > -70 * (images.length - 1)) {
      setOffset(offset - 70);
    }
  };
  // offset을 조정해서 Carousel Element 오른쪽 이동
  const moveRight = () => {
    if (offset < 0) {
      setOffset(offset + 70);
    }
  };

  // Effect
  // offset 조정 시 실제로 이동
  // offset 에 따라 이동 버튼 활성화
  useEffect(() => {
    const carousel = document.querySelectorAll(".ImgCarousel-image");
    carousel.forEach(element => {
      element.style.left = `${offset}vw`;
    });

    const leftArrow = document.querySelector(".carouselArrow__right");
    const rightArrow = document.querySelector(".carouselArrow__left");
    if (offset <= -70 * (images.length - 1)) {
      leftArrow.style.display = "none";
      rightArrow.style.display = "inline";
    } else if (offset >= 0) {
      rightArrow.style.display = "none";
      leftArrow.style.display = "inline";
    } else {
      leftArrow.style.display = "inline";
      rightArrow.style.display = "inline";
    }
  }, [offset, images.length]);

  return (
    <div className="ArchivedItem__ImgCarousel">
      <div className="ImgCarousel-images">
        {images.map((image, img_idx) => (
          <img
            className="ImgCarousel-image"
            src={image}
            alt="archive_img"
            key={img_idx}
          />
        ))}
      </div>

      <img
        className="carouselArrow carouselArrow__left"
        src="/images/left-arrow.png"
        alt="left_arrow"
        onClick={moveRight}
      />
      <img
        className="carouselArrow carouselArrow__right"
        src="/images/right-arrow.png"
        alt="right_arrow"
        onClick={moveLeft}
      />
    </div>
  );
}
