import React from "react";
import "./ArchivedItem.css";
import ArchivedItemImgCarousel from "./ArchivedItem__ImgCarousel";

function ArchivedItem({ archive }) {
  return (
    <div className="Archived-item">
      <h3>{archive.title}</h3>
      {archive.details.map((detail, idx) => (
        <div key={idx}>
          <div className="Archived-item__date">{detail.date}</div>
          <ArchivedItemImgCarousel images={detail.images} />
          <div className="Archived-item__content">
            {detail.content.split("\n").map((element, idx) => (
              <div key={idx}>{element}</div>
            ))}
          </div>
          <hr />
        </div>
      ))}
    </div>
  );
}

export default ArchivedItem;
