import React from "react";
import { Link } from "react-router-dom/cjs/react-router-dom.min";
import "./Home.css";

function Home() {
  return (
    <div className="Home">
      {/* 왼쪽 */}
      <div className="Home__left">
        <div>
          <div className="Home__profile-img">사진사진</div>
          <div>약력</div>
        </div>
      </div>
      {/* 오른쪽 */}
      <div className="Home__right">
        <div>
          <p>안녕하세요! 개발자 박정웅입니다!</p>
          <p>소개글</p>
          <div>
            <p>경력 사항</p>
            <Link to="/profile">자세히 보러가기</Link>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Home;
