import React from "react";
import { Link } from "react-router-dom";
import "./Header.css";

function Header() {
  return (
    <div className="Header">
      {/* <div>Home</div> */}
      <Link to="/">Home</Link>
      <Link to="/profile">Profile</Link>
      <Link to="/articles">Articles</Link>
      <Link to="archives">Archives</Link>
    </div>
  );
}

export default Header;
